import React, { FunctionComponent, useState, useEffect } from "react";
import "./IndexPage.scss";

const IndexPage: FunctionComponent = () => {
  const [faderClass, setFaderClass] = useState("Fader");

  useEffect(() => {
    // Fader animation
    setFaderClass("Fader fading");
    const timer = setTimeout(() => setFaderClass("Fader revealing"), 5000);
    const timer2 = setTimeout(() => setFaderClass("Fader revealed"), 6000);

    return () => {
      clearTimeout(timer);
      clearTimeout(timer2);
    };
  }, []);

  return (
    <div className="App">
      <div className={faderClass} />
      <div className="MainBody">
        <p>Fader load test successful</p>
      </div>
    </div>
  );
};

export default IndexPage;
